

import { ref } from "vue";
export default {
  name: "RateResponse",
  props: {
    quotation: {
      type: Array,
      default: undefined,
    },
  },
  setup(props){
    const content = ref(false);
    const totalRate = () => {
      let rate = 0;
      props.quotation.forEach(function (value, index) {
        rate += Number(value.precio_unidad)
      });
      return rate;
    }
    return{
      content,
      totalRate,
    };
  },
};
